.home {
  text-align: center;
  padding: 20px;
}

.home h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.home p {
  font-size: 1.2em;
  margin-bottom: 30px;
}

.features {
  margin-top: 20px;
}

.features h2 {
  font-size: 2em;
  margin-bottom: 10px;
}

.features ul {
  list-style: none;
  padding: 0;
  font-size: 1.1em;
}

.features ul li {
  margin: 10px 0;
}

.cta {
  margin-top: 30px;
}

.cta-button {
  padding: 10px 20px;
  font-size: 1em;
  color: white;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #2980b9;
}
