.register {
    text-align: center;
    padding: 20px;
  }
  
  .register h2 {
    font-size: 2em;
    margin-bottom: 20px;
  }
  
  .register form {
    display: inline-block;
    text-align: left;
  }
  
  .register div {
    margin-bottom: 15px;
  }
  
  .register label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .register input {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
  }
  
  .register button {
    padding: 10px 20px;
    font-size: 1em;
    color: white;
    background-color: #4CAF50;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .register button:hover {
    background-color: #45a049;
  }
  